<template>
    <div class="cell" @click="flagClick">
        <div class="box">
            <div v-if="flag"></div>
            <div v-else>{{text}}</div>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
            flag: true,
            text: 'X'
        } 
    },
    props:['num'],
    methods:{
        flagClick () {
            if (!this.flag) return
            this.flag = !this.flag
            this.text = this.num % 2 === 0 ? 'X' : 'O'
            this.$emit('cellClick',this.text)
        }
    }
}
</script>

<style>
.box {
    width: 100px;
    height: 100px;
    border: 1px solid grey;
    text-align: center;
    line-height: 100px;
    font-size: 60px;
}
</style>