<template>
  <div id="app" @click="num++">
    <div class="row">
      <Cell :num=num @cellClick="appDoit(0,$event)" />
      <Cell :num=num @cellClick="appDoit(1,$event)" />
      <Cell :num=num @cellClick="appDoit(2,$event)" />
    </div>
    <div class="row">
      <Cell :num=num @cellClick="appDoit(3,$event)" />
      <Cell :num=num @cellClick="appDoit(4,$event)" />
      <Cell :num=num @cellClick="appDoit(5,$event)" />
    </div>
    <div class="row">
      <Cell :num=num @cellClick="appDoit(6,$event)" />
      <Cell :num=num @cellClick="appDoit(7,$event)" />
      <Cell :num=num @cellClick="appDoit(8,$event)" />
    </div>
  </div>
</template>

<script>
import Cell from './components/Cell'

export default {
  name: 'App',
  data () {
    return {
      num:0,
      map:[
        [null,null,null],[null,null,null],[null,null,null]
      ],
    }
  },
  components: {
    Cell
  },
  methods: {
    teller () {
      // 判断横向相同
      for (let i = 0; i <= 2; i++) {
        if (
            this.map[i][0]!==null && 
            this.map[i][0] === this.map[i][1] && 
            this.map[i][1] === this.map[i][2]
          ) {
            setTimeout(()=>{
              alert(`恭喜，${this.map[i][0]}获胜！`)
            })
        }
      }
      // 判断竖向相同
      for (let j = 0; j <= 2; j++) {
          if (
            this.map[0][j]!==null &&
            this.map[0][j] === this.map[1][j] &&
            this.map[1][j] === this.map[2][j]
          ) {
            setTimeout(()=>{
               alert(`恭喜，${this.map[0][j]}获胜！`)
            })

          }
      }
      // 判断斜向相同
      if (this.map[0][0] !== null && this.map[0][0] === this.map[1][1] && this.map[1][1] === this.map[2][2]) {
        setTimeout(()=>{
          alert(`恭喜，${this.map[0][0]}获胜！`)
        })
        
      }
      if (this.map[0][2] !== null && this.map[0][2] === this.map[1][1] && this.map[1][1] === this.map[2][0]) {
        setTimeout(()=>{
          alert(`恭喜，${this.map[0][2]}获胜！`)
        })
      }


    },
    appDoit (number,text) {
      this.map[Math.floor(number/3)][number%3] = text
      this.teller()
    }
  }
}
</script>

<style>
.row{
  display: flex;
}
</style>
